<script>

  import {template_filters} from '../../../utils/template-filters';

  const DEBUG = true;

  export default {
    props: [
      'is_compact',
      'onair',
      'is_current',
      'schedule_item',
    ],
    components: {},
    data() {
      return {
        is_hover: false
      }
    },
    mounted: function () {
      if (DEBUG) console.debug('media - mounted');
    },
    computed: {

      item() {
        return this.schedule_item.item;
      },

      emission() {
        return this.schedule_item.emission;
      },

      is_onair() {
        return (this.schedule_item && this.schedule_item.uuid === this.onair);
      },

      is_visible() {
        return (this.is_current || ! this.is_compact);
      },

    },
    methods: {
      play: function (schedule_item) {
        const _c = {
          do: 'play',
          item: schedule_item
        };
        this.$emit('controls', _c);
      },
      pause: function () {
        const _c = {
          do: 'pause'
        };
        this.$emit('controls', _c);
      },
      stop: function () {
        const _c = {
          do: 'stop'
        };
        this.$emit('controls', _c);
      },
    },
    filters: template_filters,
  }

</script>

<style lang="scss" scoped>
    @import '../../../../sass/site/settings';

    .media {
        display: flex;
        padding: 0 8px;
        min-height: 38px;
        align-items: center;

        color: rgba(#fff, .7);


        &__airtime {
            min-width: 60px;
        }

        &__controls {
            .action {
                cursor: pointer;
            }
            padding: 0 10px;
        }

        &__meta {
            flex-grow: 1;
            display: flex;
            &__title {

            }
            &__by {
                padding: 0 6px;
                opacity: .5;
            }
            &__artist {

            }
        }

        &.is-current {
            color: #fff;
        }

        &.is-expanded {
            border-top: 1px solid rgba(#fff, 0.4);
            &:hover {
                color: #fff;
                background: rgba(#fff, 0.1);
            }
        }

        &.is-expanded & {
            &__meta {
                &__title {
                    width: 50%;
                }
            }
        }

    }

</style>

<template>
    <transition name="fade" mode="out-in">
        <div class="media"
             v-if="is_visible"
             @mouseover="is_hover=true"
             @mouseleave="is_hover=false"
             v-bind:class="{ 'is-current': is_current,  'is-compact': is_compact,  'is-expanded': (! is_compact) }">

            <div class="media__airtime">
                <div v-if="is_onair">
                    <span>On Air</span>
                </div>
                <div v-else>
                    <span>{{ schedule_item.time_start | datetime2hhmm }}</span>
                </div>
            </div>

            <div class="media__controls">
                <span @click="play(schedule_item)" class="action">
                    <i class="fa fa-play"></i>
                </span>
                <span @click="pause(schedule_item)" class="action">
                    <i class="fa fa-pause"></i>
                </span>
                <span @click="stop(schedule_item)" class="action">
                    <i class="fa fa-stop"></i>
                </span>
            </div>

            <div class="media__meta">
                <div class="media__meta__title">
                    <span>{{ item.name }}</span>
                </div>
                <div v-if="is_compact" class="media__meta__by">
                    by
                </div>
                <div class="media__meta__artist">
                    <span>{{ item.artist.name }}</span>
                </div>
            </div>

        </div>
    </transition>
</template>
