import {wrapRequest} from "./requests";
import APIClient from "../api/client";

const DEBUG = true;

const state = {
  sm2: null
};
const getters = {
  sm2: state => state.sm2
};
const mutations = {
    sm2 : (state, payload) => {
      state.sm2 = payload;
    },
};

const actions = {
    sm2: async (context, payload) => {
      if (DEBUG) console.debug('actions - sm2', payload);
      context.commit('sm2', payload);
    },
};

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions,
};
