<script>


  import {static_proxy_prefix} from '../../../api/utils';

  const DEBUG = true;


  export default {
    name: 'ScheduleItem',
    props: {
      schedule_item: Object,
      index: Number, // index of this object in schedule array
      offset: Number, // view offset
      onair: String // UUID currently on air or null
    },
    data() {
      return {
        votes_up: 7,
      }
    },
    mounted: function () {

    },
    computed: {

      item() {
        return this.schedule_item.item;
      },

      emission() {
        return this.schedule_item.emission;
      },

      relative_offset() {
        return (this.index - this.offset)
      },

      visible() {
        // we have 2 visible positions at the right side & 4 at the left side
        // to keep things more or less fast elements have to be removed from dom
        return (this.relative_offset < 15 && this.relative_offset > -30)
      },

      transform() {

        let x = 0;
        let scale = 1;
        let opacity = 1;

        const o = this.relative_offset;

        if (o <= -1) {
          x = 1850;
          // x = 500;
          scale = 1.3;
          // scale = 1.1;
        }

        if (o === 1) {
          x = -140;
          scale = 0.9;
          opacity = 0.9;
        }

        if (o === 2) {
          x = -280;
          scale = 0.7;
          opacity = 0.7;
        }

        if (o === 3) {
          x = -400;
          scale = 0.5;
          opacity = 0.1;
        }

        if (o === 4) {
          x = -500;
          scale = 0.1;
          opacity = 0.01;
        }

        if (o > 4) {
          x = -500;
          scale = 0.1;
          opacity = 0;
        }

        return {
          opacity: opacity,
          zIndex: 50 - o,
          transform: `rotateY(0deg) translateX(${x}px) scale(${scale})`
        }
      },
      key_image() {
        if (this.item && this.item.release && this.item.release.main_image) {
          return static_proxy_prefix(this.item.release.main_image);
        } else {
          return null;
        }
      },
      is_onair() {
        return (this.schedule_item && this.schedule_item.uuid === this.onair);
      },
      is_current() {
        return this.relative_offset === 0;
      },
      is_history() {
        return this.relative_offset > 0;
      }

    },
    methods: {
      select: function () {
        if (!this.is_history) {
          return;
        }
        this.$emit('select', this.relative_offset * -1);
      },
      play: function () {
        console.info('play');
        const _c = {
          item: this.schedule_item,
          // do: (this.is_onair) ? 'listen' : 'play'
          do: 'play'
        };
        const _e = new CustomEvent('player:controls', {detail: _c});
        window.dispatchEvent(_e);
      }
    }
  }
</script>
<style lang="scss" scoped>
    @import '../../../../sass/site/settings';

    .schedule-item {
        background: rgba(#fff, 1.0);
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 5;
        display: flex;
        transition: transform 500ms, filter 500ms, opacity 500ms;

        &__visual {
            width: 100%;
            height: 100%;
            position: relative;

            img {
                width: 100%;
                height: 100%;
            }

            .actions {
                //background: yellow;
                position: absolute;
                top: 0;
                left: 0;
                height: 100%;
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: center;

                .action {

                    display: flex;
                    align-items: center;
                    justify-content: center;
                    width: 250px;
                    height: 250px;
                    cursor: pointer;

                    transition: background 200ms;

                    filter: drop-shadow(4px 4px 10px rgba(#000, .3));

                    &--listen {
                        background: url('/static/onair/img/bg.action.listen.png');
                    }

                    &__text {
                        color: #fff;
                        font-size: 30px;
                        text-rendering: auto;
                        transition: color 500ms;
                    }

                    &:hover {
                        &.action--listen {
                            background: url('/static/onair/img/bg.action.listen.white.png');
                        }

                        .action__text {
                            color: #000;
                        }
                    }

                }

            }

            // debugging only
            .panel {
                position: absolute;
                color: white;
                background: #000;
                left: 0;
                bottom: 0;
            }
        }

        &.is-history {
            cursor: pointer;
            filter: grayscale(100%);

            &:hover {
                filter: grayscale(0);
                transition-duration: 200ms;
            }
        }

    }

    // TODO: this is kind of redundant. at the moment used to have
    // same transition on element creation.
    .appear-enter-active {
        transition: all 500ms;
    }

    .appear-enter {
        opacity: 0.5;
        transform: rotateY(0deg) translateX(1850px) scale(1.3) !important;
    }

    // transition for actions
    .fade-enter-active, .fade-leave-active {
        transition: opacity 500ms;
    }

    .fade-enter, .fade-leave-to {
        opacity: 0;
    }

</style>

<template>
    <transition name="appear">
        <div v-if="visible"
             class="schedule-item"
             @click="select"
             v-bind:class="{ 'is-onair': is_onair, 'is-current': is_current, 'is-history': is_history }"
             v-bind:style="transform">
            <div class="schedule-item__visual">
                <picture>
                    <source :srcset="key_image">
                    <img :src="key_image">
                </picture>
                <transition name="fade">
                    <div v-if="is_current" class="actions">
                        <div @click.prevent="play" class="action action--listen">
                            <div v-if="is_onair" class="action__text">Listen</div>
                            <div v-else class="action__text">Play</div>
                        </div>
                    </div>
                </transition>
                <!---->
                <div class="panel">
                    On-Air: {{ is_onair }}<br>
                </div>
            </div>
        </div>
    </transition>
</template>
