<script>

  const DEBUG = true;
  import soundmanager from 'soundmanager2/script/soundmanager2-html5';

  import Media from './components/media.vue';

  export default {
    name: 'PlayerApp',
    components: {
      Media
    },
    props: [],
    mounted() {
      this.initialize_player();

      window.addEventListener('player:controls', (e) => {
        const action = e.detail;
        this.controls(action);

        // TODO: implement logic...
        // if (action.do && action.do === 'listen') {
        //   console.warn('PLAY STREAM');
        //
        //   this.player.stop();
        //
        //   this.player.play({
        //     url: this.stream_url,
        //     type: 'audio/mp3',
        //     whileplaying: () => {
        //       // console.debug('PlayerApp - whileplaying:', this.player);
        //       // this.$store.dispatch('player/sm2', this.player);
        //     }
        //   });
        //
        // }

      }, false);

    },
    data() {
      return {
        player: null,
        mode: 'live',
        current_uuid: null,
        visible: true,
        is_compact: false,
        stream_url: 'https://www.openbroadcast.org/stream/openbroadcast',

        // sm2 debug
      }
    },
    computed: {
      schedule() {
        return this.$store.getters['onair/schedule'];
      },
      onair() {
        return this.$store.getters['onair/onair'];
      },

      loaded_schedule_item() {
        if (this.schedule.length < 1) {
          return null;
        }
        // in live mode we always follow the most recent item
        if(this.mode === 'live') {
          return this.schedule[0];
        }
      },

      is_playing() {
        if(!this.player) {
          return false;
        }
      },

      is_paused() {
        if(!this.player) {
          return false;
        }
      },

      is_stopped() {
        if(!this.player) {
          return true;
        }
      }

    },

    methods: {

      controls: function (action) {
        if (DEBUG) console.debug('controls - action', action);

        if (action.do === 'play') {

          let opts = {
            url: null,
            type: 'audio/mp3',
            whileplaying: () => {
              // console.debug('PlayerApp - whileplaying:', this.player);
            }
          };


          let url = null;
          // check if stream or on-demand
          if (action.item && action.item.uuid === this.onair) {
            this.mode = 'live';
            opts.url = this.stream_url;

          } else {
            this.mode = 'on-demand';
            opts.url = action.item.item.stream.uri

            console.debug(action.item)

          }

          console.debug('opts', opts);

          this.player.stop();
          this.player.play(opts);

          this.current_uuid = action.item.uuid;
        }
        if (action.do === 'stop') {
          this.player.unload();
          this.player.stop();
          this.current_uuid = null;
        }

      },

      /**********************************************************
       * initialize player backend
       **********************************************************/
      initialize_player: function () {
        if (DEBUG) console.debug('PlayerApp: - initialize_player');
        soundManager.setup({
          forceUseGlobalHTML5Audio: true,
          html5PollingInterval: 100,
          debugMode: DEBUG,
          onready: () => {
            if (DEBUG) console.debug('PlayerApp: - soundManager ready');
            this.player = soundManager.createSound({
              multiShot: false,
              id: 'player_app_player'
            });
          }
        });
      }
    }
  }
</script>

<style lang="scss" scoped>
    @import '../../../sass/site/settings';
    @import '~foundation-sites/scss/foundation';

    $player-max-height: 420px;

    .player-app {
        position: fixed;
        bottom: 0;
        width: 100%;
        z-index: 99;
        color: #fff;

        max-height: $player-max-height;
        * {
            max-height: inherit;
        }

        transition: background 0ms;
        transition-delay: 0ms;

        &.is-expanded {
            background: #000;
            transition: background 500ms;
            transition-delay: 0ms;
        }
    }

    .player-container {
        @include xy-grid-container(960px, 0);
    }

    .playlist {
        background: #000;
        position: relative;

        &__header {
            display: flex;
            color: rgba(#fff, 0.7);
            padding: 0 8px;
            min-height: 40px;
            align-items: center;

            div {
                &:first-child {
                    width: 120px;
                }

                &:last-child {
                    display: flex;
                    flex-grow: 1;

                    span:first-child {
                        width: 50%;
                    }
                }
            }
        }

        &__toggle {
            position: absolute;
            right: 0;
            top: 0;
            color: #fff;
        }

        &__media {
            max-height: $player-max-height - 40px;
            overflow-x: hidden;
            overflow-y: auto;

            // TODO: create scrollbar mixin
            &::-webkit-scrollbar {
                width: 4px;
            }
            &::-webkit-scrollbar-track {
                background-color: transparent;
            }
            &::-webkit-scrollbar-thumb {
                background-color: #fff;
            }

        }
    }

</style>

<template>
    <div class="player-app" v-if="visible" v-bind:class="{ 'is-compact': is_compact,  'is-expanded': (! is_compact) }">
        <div class="player-container">

            <div div class="playlist">

                <div v-if="(! is_compact)" class="playlist__header">
                    <div>Airtime</div>
                    <div>
                        <span>Title</span>
                        <span>Artist</span>
                    </div>
                </div>
                <!--
                <h1 v-if="loaded_schedule_item">// {{loaded_schedule_item.item.name}}</h1>
                -->


                <div>
                    <span>is_playing: {{ is_playing }}</span>
                    <span>is_paused: {{ is_paused }}</span>
                    <span>is_stopped: {{ is_stopped }}</span>
                </div>
                <hr>
                <div v-if="player">

                    <span>playState: {{ player.playState }}</span><br>
                    <span>readyState: {{ player.readyState }}</span>
                </div>

                <h1>*{{ mode }} | {{ current_uuid }}*</h1>
                <div v-if="(! schedule || schedule.length < 1)">
                    NO SCHEDULE
                </div>

                <div class="playlist__media">
                    <media
                            v-for="(schedule_item, index) in schedule"
                            @controls="controls($event)"
                            v-bind:key="schedule_item.uuid"
                            v-bind:index="index"
                            v-bind:is_compact="is_compact"
                            v-bind:onair="onair"
                            v-bind:is_current="(loaded_schedule_item && loaded_schedule_item.uuid === schedule_item.uuid)"
                            v-bind:schedule_item="schedule_item"></media>
                </div>

                <div class="playlist__toggle">
                    <span v-on:click='is_compact = !is_compact'>Open/Close</span>
                </div>

            </div>
        </div>
    </div>
</template>
